.jq-snake {
    position: relative;
    overflow: hidden;
    height: 300px;
    img {
        width: 100%;
        height: 100%;
    }
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.7);
        border: 5px solid #fff;
        text-align: center;
        .disc {
            position: absolute;
            color: #fff;
            width: 100%;
            top: 50%;
            @include translateY(-50%);
        }
    }
}

