.mixitup {
    padding: 60px 0px;
    background-color: #f1f1f1;
    h3.h2 {
        text-align: center;
        margin-bottom: 40px;
        font-size: 40px;
    }
    .parent-mixitup {
        img {
            width: 100%;
        }
        // controls button
        div.controls {
            text-align: center;
            margin-bottom: 60px;
            // button
            button:focus {
                outline: none;
            }
            button.mixitup-btn-style {
                background: $main-color;
                border: 1px solid $main-color;
                color: $color-fff;
                font-size: $font-page;
                text-transform: uppercase;
                font-weight: bold;
                padding: 10px 30px;
                margin-bottom: 5px;
                cursor: pointer;
                @include transitionAll(all, 0.5s, linear);
                @include borderRadius(5px);
            }
            button:hover,
            button.mixitup-control-active {
                background: $color-fff;
                border: 1px solid $main-color;
                color: $main-color;
            }
        }
        // div parent item
        div.portfolio-item {
            div.single-item {
                margin-bottom: 30px;
                display: inline-block;
                vertical-align: middle;
                position: relative;
                //
                div.img-erea {
                    img {
                        width: 100%;
                    }
                }
                div.img-disc {
                    @include overlay;
                    display: grid;
                    justify-content: center;
                    align-items: center;
                    text-align: center;
                    background-color: rgba(0, 0, 0, 0.5);
                    @include transitionAll(all, 0.3s, linear);
                    cursor: pointer;
                    @include scaleY(0);
                    transform-origin: center center;
                    opacity: 0;
                    visibility: hidden;
                    color: $color-fff;
                    h3.h2 {
                        color: $main-color;
                        text-transform: uppercase;
                        font-size: $font-page + 4;
                        margin: 10px;
                    }
                    p.lead {
                        color: $color-fff;
                        font-size: $font-page;
                        padding: 0px 25px;
                    }
                }
                &:hover .img-disc {
                    @include scaleY(1);
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }
}

