            display: none;
        }
    }
}

// End Toggle Products Description
// Start Product View
.view-options {
    display: flex;
    justify-content: center;
    .icon-disc-word {
        position: relative;
        color: $main-color;
        &::before {
            content: attr(data-text);
            text-align: center;
            position: absolute;
            top: 0;
            left: 50%;
            margin-left: -50%;
            z-index: 1;
            padding: 5px 10px;
            font-size: 16px;
            font-weight: 500;
            opacity: 0;
            min-width: 100px;
            pointer-events: none;
            box-shadow: 0 10px 10px rgba(0, 0, 0, 0.1);
            transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -webkit-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -moz-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -ms-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -o-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            @include borderRadius(5px);
        }
        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 50%;
            z-index: 1;
            opacity: 0;
            pointer-events: none;
            transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -webkit-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -moz-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -ms-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            -o-transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
            border-style: solid;
            border-width: 10px;
            border-color: $main-color transparent transparent transparent;
        }
        &:hover::before {
            background-color: $main-color;
            color: white;
            @include transition(0.5s);
            top: -50px;
            opacity: 1;
            pointer-events: auto;
        }
        &:hover::after {
            color: white;
            @include transition(0.5s);
            top: -17px;
            opacity: 1;
        }
        i {
            margin: 10px;
            cursor: pointer;
        }
        i.active {
            color: #1abc9c;
        }
    }
}

// Grid
.products.products-grid-view {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(49%, 1fr));
    gap: 0px 10px;
    .product {
        background-color: #fff;
        padding: 10px;
        position: relative;
        img {
            height: 140px;
            width: 100%;
        }
        i {
            display: block;
            padding: 10px;
            background-color: rgb(128, 91, 91);
            margin: 10px 0;
            cursor: pointer;
        }
        .lead {
            padding: 10px;
            display: none;
        }
        img+p {
            display: none !important;
        }
    }
}

// List
.products.products-list-view {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(100%, 1fr));
    gap: 0px 10px;
    .product {
        background-color: #fff;
        padding: 10px;
        height: 200px;
        margin: 10px 0;
        position: relative;
        display: flex;
        div:first-child {
            order: 1;
            padding-left: 20px;
        }
        img {
            max-width: 100%;
            height: 100%;
        }
        i+.lead {
            display: none;
        }
        i {
            display: none;
        }
        .lead {
            padding: 10px 0;
            // display: block;
        }
    }
}

