// navbar bootstrap
nav.navbar {
    background-color: #fff;
    // @include md-media {
    // width: 70%;
    // }
    @include transitionAll(all, 0.5s, ease);
    // a like logo
    .navbar-brand {
        // margin-left: 20px;
        @include xs-media {
            // margin-left: 45%
        }
    }
    // button dropdown menu for mobail phone
    .navbar-toggler {
        border: none;
        // span = img
        .navbar-toggler-icon {
            color: #333;
        }
    }
    // div = container for ul
    div.collapse.navbar-collapse {
        @include sm-media {
            margin-left: 50px;
        }
        // ul link
        ul.navbar-nav {
            transition: all 0.5s linear;
            @include xs-media {
                // if you went to li start left in rtl
                // align-items: flex-start;
                // if you went to li start left in rtl
                // align-items: flex-end;
            }
            // li
            li.nav-item {
                // a
                a.nav-link {
                    padding: 25px 22px;
                    line-height: 1;
                    font-size: 11px;
                    text-transform: uppercase;
                    letter-spacing: 1px;
                    color: #666;
                    position: relative;
                }
                // a active
                a.nav-link.active {
                    &::before {
                        width: 10px;
                        height: 2px;
                        content: '';
                        position: absolute;
                        background: #000;
                        top: 8px;
                        left: 4px;
                        transition: all 300ms linear;
                    }
                    &::after {
                        width: 2px;
                        height: 10px;
                        content: '';
                        position: absolute;
                        background: #000;
                        top: 8px;
                        left: 4px;
                    }
                }
            }
            // li dropdown
            li.nav-item.dropdown {
                // a link name
                a.nav-link.dropdown-toggle {}
                // div dropdown
                ul.dropdown-menu {
                    border: none;
                    border-bottom: 4px solid #000;
                    li {
                        padding: 5px 0px;
                        // a item
                        a.dropdown-item {
                            text-decoration: none;
                            text-transform: uppercase;
                            font-size: 11px;
                            letter-spacing: 1px;
                            color: #666;
                            &:hover {
                                background-color: #fff;
                            }
                        }
                    }
                }
            }
        }
        // form
        form.d-flex {
            input.form-control.me-2 {}
            button.btn.btn-outline-success {}
        }
    }
}

.css-menu-hover-1 {
    ul {
        li {
            // transform-style: preserve-3d;
            // display: inline-block;
            a {
                position: relative;
                display: block;
                color: #fff;
                // font-size: 18px;
                // font-family: poppins;
                // text-transform: uppercase;
                // text-decoration: none;
                // font-weight: bold;
                // background-color: #1abc9c;
                span {
                    // display: block;
                    // padding: 15px 30px;
                    // width: 100%;
                    // height: 100%;
                    &::before {
                        content: attr(title);
                        position: absolute;
                        top: 0;
                        left: 0;
                        background-color: tomato;
                        color: #fff;
                        padding: 10px 0px;
                        transform-style: preserve-3d;
                        @include transition(0.4s);
                        transform: rotateX(90deg) translateY(150px);
                        -webkit-transform: rotateX(90deg) translateY(150px);
                        -moz-transform: rotateX(90deg) translateY(150px);
                        -ms-transform: rotateX(90deg) translateY(150px);
                        -o-transform: rotateX(90deg) translateY(150px);
                        transform-origin: bottom;
                        width: 100%;
                        height: 100%;
                        @include xs-media {
                            width: 18%;
                            direction: rtl;
                            right: 0;
                            text-align: center;
                        }
                        @include sm-media {
                            width: 15%;
                            direction: rtl;
                            right: 0;
                            text-align: center;
                        }
                        @include md-media {
                            width: 100%;
                            text-align: center;
                        }
                    }
                    &:hover::before {
                        transform: rotateX(0deg) translateY(0px);
                        -webkit-transform: rotateX(0deg) translateY(0px);
                        -moz-transform: rotateX(0deg) translateY(0px);
                        -ms-transform: rotateX(0deg) translateY(0px);
                        -o-transform: rotateX(0deg) translateY(0px);
                    }
                }
            }
        }
    }
}

